<template>
	<div class="comfirm">
		<div class="comfirm-box">
      <h2 class="tips">确定清空全部搜索历史</h2>
      <div class="handle">
        <span class="cancel" @click="cancel">取消</span>
        <span class="comfirm_1" @click="comfirm">确认</span>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
	export default {
    methods: {
      comfirm() {
        this.$emit('isClear', true)
      },
      cancel() {
        this.$emit('isClear', false)
      },
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .comfirm
    position: fixed
    top: 0
    left: 0
    right: 0
    bottom: 0
    background: rgba(125,125,125,.4)
    .comfirm-box
      padding: 20px
      width: 70%
      position: absolute
      top: 50%
      left: 50%
      transform: translate(-50%,-50%)
      background: white
      border-radius: 5px
      .tips
        margin-bottom: 40px
        color: rgba(125,125,125,1)
      .handle
        display: flex
        color: red
        text-align: right
        .cancel
          display: inline-block
          flex: 1
        .comfirm_1
          display: inline-block
          width: 70px
</style>


