const mutations = {
	SET_SINGER(state, singer) {
    state.singer = singer
	},
	SET_PLAY_LIST(state, data) {
		state.playList = data
	},
	SET_FULL_SCREEN(state, data) {
		state.fullScreen = data
	},
	SET_PLAY_MODE(state, playMode) {
		state.playMode = playMode
	},
	SET_PLAYING(state, bole) {
		state.playing = bole
	},
	SET_SEQUENCE_LIST(state, data) {
		state.sequenceList = data
	},
	SET_CUREENT_INDEX(state, index) {
		state.currendIndex = index
	},
	SET_CURRENT_DISC(state, Disc) {
        state.currentDisc = Disc
	},
	SET_CURRENT_TOPLIST(state, topList) {
		state.currentTopList = topList
	},
	SET_SEARCH_HISTORY(state, historyList) {
		state.searchHistory = historyList
	},
	SET_SEARCH_QUERY (state, query) {
		state.searchQuery = query
	},
	SET_FAVORITE_LIST(state, songList){
		state.favoriteList = songList
	},
	SET_HEARD_LIST(state, songList){
		state.heardList = songList
	}
}

export default mutations





