
export function saveSearch(arr, query) {
	let newarr = arr.filter((item)=>{
		return item !== query
	})
	newarr.unshift(query)
	return newarr
}

export function deleteSearch(arr, query) {
	let newarr = arr.filter((item)=>{
		return item !== query
	})
	return newarr
}

export function addSong(playList, currentIndex, song) {
    if (!playList.length) {
    	playList.push(song)
    	return playList
    }else{
    	console.log(1)
    	playList.splice(currentIndex + 1, 0, song)
    	return playList
    }
}


