import jsonp from 'common/js/jsonp'
import {commonParams, options} from './config'
import axios from 'axios'
const debug = process.env.NODE_ENV !== 'production'
export function getRecommend () {
	const url = 'https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg'
	const data = Object.assign({}, commonParams, {
		platform: 'h5',
		uin: 5,
		needNewCode: 1
	})
return jsonp(url, data, options)
} 

export function getPlayList(sin, ein) {
	// const url = debug ? '/api/getPlayList' : 'http://yujian.store/music/api/getPlayList'
	const url = '/api/getPlayList'
	const data = Object.assign({}, commonParams, {
    platform: 'yqq',
    hostUin: 0,
    sin: sin,
    ein: ein,
    sortId: 5,
    needNewCode: 0,
    categoryId: 10000000,
    rnd: Math.random(),
    format: 'json'
	})
	return axios.get(url, {
		params: data
	}).then((res) => {
		return Promise.resolve(res, data)
	})
}

export function getDiscList(disstid){
 // const url = debug ? '/api/getDiscList' : 'http://yujian.store/music/api/getDiscList'
  const url = '/api/getDiscList'
  const data = Object.assign({}, commonParams, {
    type: 1,
	json: 1,
	utf8: 1,
	onlysong: 0,
	disstid: disstid,
	g_tk: 5381,
	loginUin: 0,
	hostUin: 0,
	platform: 'yqq',
	needNewCode: 0,
    format: 'json'
	})
	return axios.get(url, {
		params: data
	}).then((res) => {
		return Promise.resolve(res, data)
	})
}


