<template>
	<div class="rank" ref="rank">
		<scroll :data="topList" ref="topList" class="toplist" v-show="this.topList.length" :probeType="3">
			<ul>
				<li class="item" v-for="item in topList" @click="selectItem(item)">
					<div class="icon">
						<img width="100" height="100" :src="item.picUrl"/>
					</div>
					<div class="songlist">
						<li class="song" v-for="(song,index) in item.songList">
              <span>{{index + 1}}</span>
              <span>{{song.songname}}-{{song.singername}}</span>
            </li>
					</div>
				</li>
			</ul>
		</scroll>
		<div class="loading-container" v-show="!topList.length">
      <loading></loading>
    </div>
    <router-view></router-view>
	</div>
</template>

<script type="text/javascript">
  import Scroll from 'base/scroll/scroll'
	import {getTopList} from 'api/rank'
	import {ERR_OK} from 'api/config'
  import Loading from 'base/loading/loading'
  import {playlistMixin} from 'common/js/mixin'
  import {mapGetters, mapMutations} from 'vuex'
	export default {
		name: 'rank',
		mixins: [playlistMixin],
		data() {
			return {
				topList: []
			}
		},
		methods: {
			...mapMutations(['SET_CURRENT_TOPLIST']),
      handlePlayList(playlist) {
        let bottom = playlist.length ? '50px' : 0
        this.$refs.rank.style.bottom = bottom
        this.$refs.topList.refresh()
      },
      selectItem(item){
      	this.$router.push({
      		path: `/rank/${item.id}`
      	})
      	this.SET_CURRENT_TOPLIST(item)
      }
		},
		created() {
      getTopList().then((res)=>{
       	if (res.code === ERR_OK) {
       		this.topList = res.data.topList
       	}
      })
		},
    components: {
      Scroll,
      Loading
    }
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
	.rank
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .toplist
      height: 100%
      overflow: hidden
      .item
        display: flex
        margin: 0 20px
        padding: 5px 0
        height: 100px
        border-top: 1px solid rgba(233,233,233,0.5)
        .icon
          flex: 0 0 100px
          width: 100px
          height: 100px
          img
            border-radius: 5px
        .songlist
          flex: 1
          display: flex
          flex-direction: column
          justify-content: center
          padding: 0 20px
          height: 100px
          overflow: hidden
          background: white
          color: black
          font-size: 14px
          .song
            no-wrap()
            line-height: 26px
  .loading-container{
    position: absolute
    width: 100%
    top: 50%
    transform: translateY(-50%)
  }
</style>


