<template>
	<div class="song-list">
		<ul>
			<li v-for="(song, index) in songDataes" class="song-item" @click="selectItem(song, index)">
        <div class="rank" v-show="rank">
          <span :class="getRankCls(index)">{{setText(index)}}</span>
        </div>
				<div class="content">
					<h3 class="name">{{song.name}}</h3>
					<p class="desc">{{song.singer}} · {{song.album}}</p>
				</div>
			</li>
      <loading v-if="isLoadMore"></loading>
		</ul>
		<div v-show="!songDataes.length" class="loading-container">
      <loading></loading>
    </div>
	</div>
</template>
<script type="text/javascript">
	import Loading from 'base/loading/loading'
  import { mapGetters} from 'vuex'
	export default {
    props: ['songDataes', 'rank', 'isLoadMore'],
    components: {
      Loading
    },
    data() {
      return {
      }
    },
    methods: {
      selectItem (song, index) {
        this.$emit('select', song, index)
      },
      getRankCls(index){
        if (index < 3) {
          return 'icon  icon' + index
        }else{
          return 'text'
        }
      },
      setText(index) {
        if (index < 3) {
          return 
        }else{
          return index+1
        }
      }
    },
    computed: {
    },
    created() {
      // console.log(this.songDataes)
    },
    watch: {
      songDataes(val){
        console.log(val)
      }
    }
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  @import "../../common/stylus/mixin"
  .song-list{
  	padding: 0 20px
  	position: relative
    font-size: $font-size-medium
  }
  .song-item{
  	padding: 5px 0
    display: flex
    height: 50px
    align-items:center
    border-top: 1px solid rgba(233,233,233,0.5)
  }
  .rank{
    flex: 0 0 25px
    width: 25px
    margin-right: 30px
    text-align: center
    line-height: 50px
  }
  .icon{
    display: inline-block
    width: 25px
    height: 24px
    background-size: 25px 24px
    margin-top: 12px
  }
  .icon0{
    bg-image('first')
  }
  .icon1{
    bg-image('second')
  }
  .icon2{
    bg-image('third')
  }
  .text{
    color: black
    font-size: $font-size-large}
  .content{
    flex: 1
    line-height: 20px
    overflow: hidden
  }
  .name{
    width: 100%
  	color: black
  	font-size: 16px
    no-wrap()
    margin-top: 5px
  }
  .desc{
    width: 100%
  	color: #888
    margin-top: 5px
    no-wrap() 
  }
  .loading-container{
    position: absolute
    width: 100%
    top: 100px
    left: 0
  }
</style>


