<template>
	<div class="search">
		<div class="search-box">
			<search-box @query="queryChange" :updataquery="query"></search-box>
		</div>
		<scroll ref="shortcutWrapper" class="shortcut-wrapper" :data="hotKey" :probeType="3">
			<div class="shortcutBox">
        <div class="shortcut" v-show="!query.length">
          <div class="hot-key">
            <h2 class="hot-title">热门搜索</h2>
            <ul>
              <li class="hot-key-item" v-for="item in hotKey" @click="queryChange(item.k)">
                {{item.k}}
              </li>
            </ul>
          </div>
          <div class="search-history">
            <h2 class="history-title">
              <span class="text">搜索历史</span>
              <span class="clear" @click="clearHistory">
                <i class="icon-clear"></i>
              </span>
            </h2>
            
            <search-list :searches="searchHistory" @select="queryChange" @delete="deleteHistory"></search-list>
          </div>
        </div>   
      </div>
    </scroll>

    <div class="search-result" ref="searchResult" v-show="query.length">
      <search-result  ref="searchResultBox" :query="query" @selectSinger="selectSinger" :searches="searchHistory"></search-result>
    </div>
    <comfirm v-if="comfirm" @isClear="isClear"></comfirm>
    <router-view></router-view>
	</div>
</template>

<script type="text/javascript">
	import SearchBox from 'base/search-box/search-box'
  import { ERR_OK } from 'api/config'
  import {getSearchList, getHotKey} from 'api/search'
  import SearchResult from '../../components/search-result/search-result'
  import SearchList from 'base/search-list/search-list'
  import Scroll from 'base/scroll/scroll'
  import {mapGetters, mapMutations, mapActions} from 'vuex'
  import {playlistMixin, searchMixin} from 'common/js/mixin'
  import Comfirm from 'base/comfirm/comfirm'
	export default {
    mixins: [playlistMixin],
		name: 'search',
		components: {
			SearchResult,
			SearchBox,
      Scroll,
      SearchList,
      Comfirm
		},
		data() {
			return {
				hotKey: [],
				query: '',
        comfirm: false
			}
		},
    computed: {
      ...mapGetters(['searchHistory'])
    },
		methods: {
      ...mapMutations(['SET_SINGER']),
      ...mapActions(['clearSearchHistory', 'saveSearchHistory', 'deleteSearchHistory']),
      handlePlayList(playList) {
          let bottom = playList.length ? '50px' : 0
          this.$refs.shortcutWrapper.$el.style.bottom = bottom
          this.$refs.searchResult.style.bottom = bottom
          this.$refs.shortcutWrapper.refresh()
      },
      queryChange(query) {
      	if (query) {
          this.saveSearchHistory(query)
        }
        this.query = query
      },
      selectSinger(singer) {
        this.$router.push({
          path: `/search/${singer.id}`
        })
        this.SET_SINGER(singer)
      },
      deleteHistory(query) {
        this.deleteSearchHistory(query)
      },
      clearHistory() {
        this.comfirm = true
      },
      isClear(bol) {
        this.comfirm = false
        if (bol) {
          this.clearSearchHistory()
        }else{
          return
        }
      }
		},
		created() {
			//获取热门搜索
			getHotKey().then(res => {
				if (res.code === ERR_OK) {
          this.hotKey = res.data.hotkey.slice(0, 10)
        }
			})
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .search, search-box{
    margin: 10px
  }
  .shortcut-wrapper{
  	position: fixed
    top: 143px
    left: 0
    bottom: 0
    width: 100%
    overflow: hidden
  }
  .shortcutBox{
    // height: 100%
  }
  .shortcut{
  	height: 100%
  }
  .search-history,.hot-key{
    margin: 0 10px 10px 10px
  }
  .history-title, .hot-title{
  	font-size: 15px
    font-weight: bold 
    color: black
    padding-top: 5px
    padding-bottom: 10px
  }
  .history-title .text{
    font-weight: bold 
    color: black
  }
  .hot-key-item{
  	display: inline-block
    padding: 5px 10px
    margin: 0 20px 10px 0
    border-radius: 6px
    background: #ebecec
    font-size: $font-size-medium
    color:black
  }
  .clear{
  	font-size: $font-size-medium
    color: black
    float: right
  }
  
  .search-result
      border-top: 1px solid rgba(233,233,233,0.5)
      position: fixed
      width: 100%
      top: 143px
      bottom: 0
      left: 0
      padding: 0 20px
      overflow: hidden
      box-sizing: border-box
</style>


