<template>
  <transition name="list-fade">
	<div class="play-list" ref="playList">
    <div class="mask" @click="closePlayList"></div>
    <div class="play-list-wrapper">
      <p class="play-list-head">
        <i class="play-mode" :class="playModeIcon" @click="changePlayMode"></i>
        <span class="text">{{playModeState}}</span>
        <i class="clear icon-clear" @click="clearPlayList"></i>
      </p>
      <scroll class="play-list-box" :data="sequenceList">
        <ul class="play-list-body">
          <li class="song-item" v-for="item in sequenceList" @click.stop="playSong(item)">
            <p class="text">{{item.name}}<span class="name"> - {{item.singer}}</span></p>
            <i class="icon-delete" @click.stop="deleteSong(item)"></i>
          </li>
        </ul>
      </scroll>
    </div>
    <comfirm v-if="comfirm" @isClear="isClear"></comfirm>
	</div>
  </transition>
</template>

<script type="text/javascript">
  import { playMode } from 'common/js/config'
  import {mapGetters, mapActions} from 'vuex'
  import Scroll from 'base/scroll/scroll'
  import Comfirm from 'base/comfirm/comfirm'
	export default {
    data(){
      return {
        playModeState: '',
        comfirm: false
      }
    },
    components: {
      Scroll,
      Comfirm
    },
    computed: {
      ...mapGetters(['sequenceList', 'playMode']),
      playModeIcon(){
        if (this.playMode === playMode.random) {
          this.playModeState = '随机播放'
          return 'icon-random'
        }else if(this.playMode === playMode.loop){
          this.playModeState = '单曲循环'
          return 'icon-loop'
        }else{
          this.playModeState = '列表播放'
          return 'icon-sequence'
        }
      },
    },
    methods: {
      ...mapActions(['clearPlay', 'deleteSongItem','playListSong']),
      closePlayList() {
        this.$emit('closePlayList')
      },
      clearPlayList() {
        this.comfirm = true
      },
      isClear(bol) {
        console.log(bol)
        this.comfirm = false
        if (bol) {
          this.clearPlay()
          this.closePlayList()
        }else{
          return
        }
      },
      changePlayMode(){
        this.$emit('changePlayMode')
      },
      deleteSong(item){
        this.deleteSongItem(item)
      },
      playSong(item) {
        this.playListSong(item)
      }
    },
    watch: {
      playList(val) {
        console.log(val)
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  @import "../../common/stylus/mixin"
  .play-list
    width: 100%
    position: absolute
    bottom: 0
    top: 0
    .mask
      width: 100%
      position: fixed
      bottom: 0
      top: 0
      background: rgba(0,0,0,.3)
    .play-list-wrapper
      width: 100%
      position: fixed
      bottom: 0
      top: 220px
      color: black
      background: white
      border-radius: 10px 10px 0 0
      .play-list-head
        display: flex
        font-size: 16px
        height: 50px
        line-height: 50px
        border-bottom: 1px solid rgba(211,211,211,.4)
        .play-mode
          line-height: 50px
          width: 30px
          padding-left: 10px
          font-size: 16px
        .text
          // width: 20px
        .clear
          line-height: 50px
          flex: 1
          font-size: 16px
          text-align: right
          padding-right: 10px
          color: gray
      .play-list-box
        width: 100%
        position: absolute
        bottom: 0
        top: 50px
        overflow: hidden
        .play-list-body
          padding: 0 10px
          .song-item
            font-size: 15px
            display: flex
            line-height: 40px
            border-bottom: 1px solid rgba(211,211,211,.4)
            .text
              width: 250px
              no-wrap()
              .name{
                font-size: 12px
                color: gray
              }
            .icon-delete
              flex: 1
              font-size: 14px
              text-align: right
              line-height: 40px
              color: rgba(128, 128, 128, 0.5)
.list-fade-enter-active, .list-fade-leave-active{
      transition: opacity 0.3s
    }
    .list-wrapper{
      transition: all 0.3s
    }
    .list-fade-enter, .list-fade-leave-to{
      opacity: 0
    }
</style>


