<template>
	<div class="player-wrapper" v-show="playList.length">
		<div class="normal-player" v-show="fullScreen">
      <div class="background">
        <img :src="currendSong.image" width="100%" height="100%">
      </div>
      <div class="top">
        <div class="closeFullScreen" @click.stop="closeFullScreen">
          <i class="icon-back"></i>   
        </div>
        <h2 class="title">{{currendSong.name}}</h2>
        <h3 class="subtitle">{{currendSong.singer}}</h3>
      </div>
      <div class="middle" @touchstart="middleStart" @touchmove="middleMove" @touchend="middleEnd">
        <div class="middle-l"
                    ref="cdWrapper">
          <div class="cd-wrapper">
            <div class="cd" ref="cd">
              <img :src="currendSong.image" width="100%" height="100%" class="image" :class="cdCls">
            </div>
          </div>
        </div>
        <scroll class="middle-r"
                :data="currentLyric && currentLyric.lines"
                ref="lyricList">
          <div class="lyric-wrapper">
            <div v-if="currentLyric">
              <p v-for="(item,index) in currentLyric.lines"
                 class="lyric-text"
                 ref="lyricLine"
                 :class="{'current':currentLineNum===index}"
                 v-html="item.txt">
              </p>
            </div>
            <div v-else>
              <p class="lyric-text">
                此歌曲为没有填词的纯音乐，请您欣赏
              </p>
            </div>
          </div>
        </scroll>
      </div>
      <div class="bottom">
        <div class="dot-wrapper">
            <span class="dot" :class="{'active':currentShow==='cd'}"></span>
            <span class="dot" :class="{'active':currentShow==='lyric'}"></span>
          </div>
        <div class="progress-wrapper">
          <span class="time time-l">{{format(currentTime)}}</span>
          <div class="progress-bar-wrapper">
              <progress-bar :percent="percent" @changeCurrentTime="setCurrentTime" @percentChange="percentChange"></progress-bar>
          </div>
          <span class="time time-r">{{format(currendSong.duration)}}</span>
        </div>
        <div class="operators">
          <div class="icon i-left">
            <i :class="playModeIcon" @click="changePlayMode"></i>
          </div>
          <div class="icon i-left">
            <i class="icon-prev" @click="prevSong"></i>
          </div>
          <div class="icon i-center">
            <i class="needsclick"  @click="togglePlaying" :class="playIcon"></i>
          </div>
          <div class="icon i-right">
            <i class="icon-next" @click="nextSong"></i>
          </div>
          <div class="icon i-right">
            <i class="icon" @click="favoriteSong" :class="isFavorite"></i>
          </div>

          <div class="play-mode-wrapper" v-show="isShow">
            <span class="play-mode-state">{{playModeState}}</span>
          </div>
        </div>
      </div>
		</div>
		<div class="mini-player" v-show="!fullScreen" @click="openFullScreen">
      <div class="head-icon">
        <div class="img-wrapper"  :class="cdCls">
          <img :src="currendSong.image" style="width:40px; height:40px">
        </div>
      </div>  
      <div class="text">
        <h2 class="name">{{currendSong.name}}</h2>
        <p class="desc">{{currendSong.singer}}</p>
      </div>  
      <div class="control">
        <i class="icon-play-mini icon" :class="playIconMiNi"></i>
        <div class="progress-circle-wrapper"  @click.stop="togglePlaying">
          <progress-circle :percent="percent" :radius="30" ></progress-circle>
        </div>
      </div>  
      <div class="control">
        <i class="icon-playlist icon" @click.stop="showPlayList=!showPlayList"></i>
      </div>  
    </div>
    <play-list ref="playlist" v-if="showPlayList" @closePlayList="closePlayList" @changePlayMode="changePlayMode"></play-list>
    <audio ref="audio" @canplay="ready" @error="error" @timeupdate="updateTime" @ended="end"></audio>
	</div>
</template>

<script type="text/javascript">
  import { mapGetters, mapMutations, mapActions} from 'vuex'
  import scroll from 'base/scroll/scroll'
  import ProgressBar from 'base/progress-bar/progress-bar'
  import ProgressCircle from 'base/progress-circle/progress-circle'
  import Lyric from 'lyric-parser'
  import { playMode, findIndex} from 'common/js/config'
  import PlayList from '../play-list/play-list'
	export default {
    components: {
      ProgressBar,
      ProgressCircle,
      scroll,
      PlayList
    },
		data () {
			return {
        songReady: false, //歌曲是否已经进入播放列表
        currentTime: 0,
        isShow: false,
        currentShow: 'cd',
        currentLyric: null,
        currentLineNum: 0,
        playingLyric: '',
        opacity: 1,
        showPlayList: false
      }
		},
    computed: {
      ...mapGetters(['playList', 'fullScreen', 'currendSong', 'playing', 'currendIndex', 'playMode', 'favoriteList']),
      playIcon () {
        return this.playing ?  'icon-pause' : 'icon-play'
      },
      playIconMiNi () {
        return this.playing ?  'icon-pause-mini' : 'icon-play-mini'
      },
      cdCls () {
        return this.playing ? 'play' : 'paused play'
      },
      percent() {
        return this.currentTime / this.currendSong.duration
      },
      //playMode
      playModeIcon(){
        if (this.playMode === playMode.random) {
          return 'icon-random'
        }else if(this.playMode === playMode.loop){
          return 'icon-loop'
        }else{
          return 'icon-sequence'
        }
      },
      playModeState() {
        if (this.playMode === 0) {
          return '列表循环'
        }else if(this.playMode === 1){
          return '单曲循环'
        }else{
          return '随机播放'
        }
      },
      isFavorite(){
        let index = findIndex(this.favoriteList, this.currendSong)
        if (index >= 0) {
          return 'icon-favorite'
        }else{
         return 'icon-not-favorite'
        }
      }
    },
    methods: {
      ...mapMutations(['SET_FULL_SCREEN', 'SET_PLAYING', 'SET_CUREENT_INDEX', 'SET_PLAY_MODE', 'SET_PLAY_LIST']),
      ...mapActions(['resetPlayList', 'selectFavoriteSong', 'selectHeardList']),
      closeFullScreen() {
        this.SET_FULL_SCREEN(false)
      },
      openFullScreen() {
        this.SET_FULL_SCREEN(true)
      },
      setCurrentTime(percent) {
        let currentTime = this.currendSong.duration * percent
        this.$refs.audio.currentTime = currentTime
        if (this.currentLyric) {
          this.currentLyric.seek(currentTime*1000)
        }
      },
      // 点击播放或者暂停安扭
      togglePlaying() {
        let audio = this.$refs.audio
        // console.log(audio.paused)
        if (audio.paused) {
          this.SET_PLAYING(true)
        }else{
          this.SET_PLAYING(false)
        }
        if (this.currentLyric) {
          this.currentLyric.togglePlay()
        }
      },
      // 歌曲正在加载中
      ready() {
        this.songReady = true
      },
      // 歌曲正在加载错误
      error() {
        console.log(1)
        this.songReady = true
      },
      end() {
        if (this.playMode === 1) {
          this.$refs.audio.load()
          this.$refs.audio.play()
          return 
        }
        this.nextSong()
      },
      nextSong () {
        if (!this.songReady) {
          return
        }
        let index = this.currendIndex + 1
        if (index === this.playList.length) {
          index = 0
        }
        this.SET_CUREENT_INDEX(index)
        this.songReady = false
      },
      prevSong () {
        let index = this.currendIndex - 1
        if (index === -1) {
          index = this.playList.length - 1
        }
        this.SET_CUREENT_INDEX(index)
        this.songReady = false
      },
      // 获取当前歌曲播放的位置
      updateTime(e) {
        this.currentTime = this._pad(e.target.currentTime)
      },
      percentChange(percentChange) {
        let currentTime = this.currendSong.duration * percentChange
        this.currentTime = currentTime
        if (this.currentLyric) {
          this.currentLyric.seek(currentTime*1000)
        }
      },
      // 时间秒：补位
      _pad(num, n = 2) {
        let len = num.toString().length
        while (len < n) {
          num = '0' + num
          len++
        }
        return num
      },
      // 获取当前播放歌曲时长
      format(time) {
        time = time| 0
        const minute = time/60 | 0
        const second = this._pad(time % 60)
        return minute + ':' + second
      },
      // 改变播放模式
      changePlayMode() {
        this.isShow = true
        setTimeout(()=>{
          this.isShow = false
        },2000)
        let mode = this.playMode + 1
        if (mode > 2) {
          mode = 0
        }
        this.SET_PLAY_MODE(mode)
        // 当切换至随机播放模式时，改变播放列表的顺序
        if (this.playMode !== 1) {
          this.resetPlayList({
            list: this.playList
          })
        }
      },

      // 切换歌曲歌词和图片
      middleStart(e){
        this.touch.initiated = true
        this.touch.moved = false
        const touch = e.touches[0]
        this.touch.startX = touch.pageX
        this.touch.startY = touch.pageY
      },
      middleMove(e){
        if (!this.touch.initiated) {
          return
        }
        const touch = e.touches[0]
        const deltaX = touch.pageX - this.touch.startX
        const deltaY = touch.pageY - this.touch.startY
        // x轴移动的距离大于y轴时，表示滚动歌词
        if (Math.abs(deltaY) > Math.abs(deltaX)) {
          return
        }
        if (!this.touch.moved) {
          this.touch.moved = true
        }
        const left = this.currentShow === 'cd' ? 0 : -window.innerWidth
        const offsetWidth = Math.min(0, Math.max(-window.innerWidth, left + deltaX))
        this.$refs.lyricList.$el.style.transform = `translate3d(${offsetWidth}px,0,0)`
        // 设置cd页的透明度
        this.touch.percent = Math.abs(offsetWidth / window.innerWidth)
        this.$refs.cdWrapper.style.opacity = 1 - this.touch.percent
      },
      middleEnd(e){
        if (!this.touch.moved) {
          return
        }
        if (this.currentShow === 'cd') {
          this.$refs.lyricList.$el.style.transform = `translate3d(${-window.innerWidth}px,0,0)`
          this.$refs.cdWrapper.style.opacity = 0
          this.currentShow = 'lyric'
        }else{
          this.$refs.lyricList.$el.style.transform = `translate3d(${0}px,0,0)`
          this.$refs.cdWrapper.style.opacity = 1
          this.currentShow = 'cd'
        }
        // this.$refs.lyricList.$el.style.transform = `translate3d(${offsetWidth}px,0,0)`
          // ? this.currentShow = 'lyric' : this.currentShow = 'cd'
      },
      getLyric(){
        this.currendSong.getLyric().then((lyric)=>{
          if (lyric === '[00:00:00]此歌曲为没有填词的纯音乐，请您欣赏') {}
          this.currentLyric = new Lyric(lyric, this.handleLyric)
          // 如果当前歌曲状态为播放，则播放歌词
          if (this.playing) {
            this.currentLyric.play()
          }
        })
        // 
      },
      handleLyric({lineNum, txt}) {
        if (!this.$refs.lyricLine) {
          return
        }
        this.currentLineNum = lineNum
        if (lineNum > 4) {
          let lineEl = this.$refs.lyricLine[lineNum - 4]
          this.$refs.lyricList.scrollToElement(lineEl, 1000)
        }else {
          this.$refs.lyricList.scrollTo(0, 0, 1000)
        }
        this.playingLyric = txt
      },

      // 收藏歌曲
      favoriteSong(){
        console.log(this.currendSong)
        this.selectFavoriteSong(this.currendSong)
      },
      closePlayList() {
        if (this.showPlayList) {
          this.showPlayList = false
        }
      }    },
    watch: {
      // 监听播放状态，进行切换
      playing(val){
        // console.log(val)
        const audio = this.$refs.audio
        this.$nextTick(() => {
          val ? audio.play() : audio.pause()
        })
      },
      currendSong(newSong, oldSong) {
        if (JSON.stringify(newSong) == "{}") {
          return
        }
        if (newSong.id === oldSong.id) {
          return 
        }
          this.selectHeardList(newSong)
          this.$refs.audio.src = newSong.url
          this.$refs.audio.play()
          this.SET_PLAYING(true)
        if (this.currentLyric) {
          this.currentLyric.stop()
          this.currentLyric = null
          this.currentTime = 0
          this.playingLyric = ''
          this.currentLineNum = 0
        }
        // 若歌曲 3s 未播放，则认为超时，修改状态确保可以切换歌曲。
        clearTimeout(this.timer)
        this.timer = setTimeout(() => {
          this.songReady = true
        }, 5000)

        this.getLyric()
      }
    },
    mounted() {
      // this.globalClick(this.closePlayList)
    },
    created(){
      this.touch = {}
    }
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  @import "../../common/stylus/mixin"
  .player-wrapper{
  }
  .normal-player{
  	position: fixed
  	top: 0
  	bottom: 0
  	left: 0
  	right: 0
  	background: $color-background
    z-index: 150 
  }
  .background{
    position: absolute
    top: 0
    bottom: 0
    left: 0
    right: 0
    z-index: -1
    opacity: 0.6
    filter: blur(20px)
  }
  .background img{
    width: 100%
    height: 100%
  }
  .top{
    position: relative
    text-align: center
    color: white
  }
  .closeFullScreen{
    position absolute
    width: 30px
    left: 10px
    top: 5px
  }
  .closeFullScreen .icon-back{
    display: block
    padding: 9px
    font-size: $font-size-large-x
    color: $color-theme
    transform: rotate(-90deg)
  }
  .title{
    line-height: 40px
    font-size: $font-size-large
    width: 70%;
    margin: 0 auto;
    no-wrap()
  }
  .subtitle{
    line-height: 20px
    font-size: $font-size-medium
  }
  .middle{
    width: 100%
    position: fixed
    top: 80px
    bottom: 170px
    font-size: 0
  }
  .bottom{
    position: absolute
    bottom: 50px
    width: 100%
  }
  .operators{
    position: relative
    top: 10px
    display: flex
    align-items: center
  }
  .icon{
    flex: 1
    color: $color-theme
  }
  .icon.disable{
    color: $color-theme-d
  }
  .icon i{
    font-size: 30px
  }
  .i-left{
    text-align: right
  }
  .i-center{
    padding: 0 20px
    text-align: center
  }
  .i-center i{
    font-size: 40px
  }
  .i-right{
    text-align: left
  }
  .icon-favorite{
    color: $color-sub-theme
  }
  
  .play-mode-wrapper{
    position: absolute
    top: 0
    width:100%
    text-align: center
    top: -20px
  }
  .play-mode-state{
    display: inline-block
    color: black
    background: rgba(255,255,255,.9)
    width: 80px
    line-height: 25px
    font-size: 12px
    border-radius: 15px
  }
  

  .mini-player{
  	position: fixed
  	bottom: 0
  	left: 0
  	right: 0
  	height: 49px
  	background: white
    display: flex
    align-items: center
    border-top: 1px solid rgba(233,233,233,.5)
  }
  .middle-l{
    display: inline-block
    position: relative
    top: 10px
    width: 100%
    height: 0
    padding-top: 80%
  }
  .cd-wrapper{
    position: absolute
    left: 10%
    top: 0
    width: 80%
    box-sizing: border-box
    height: 100%
    border-radius: 100%
  }
  .cd{
    width: 100%
    height: 100%
    border-radius: 50%
    }
  .image{
    position: absolute
    left: 0
    top: 0
    width: 100%
    height: 100%
    box-sizing: border-box
    border-radius: 50%
    border: 10px solid rgba(255, 255, 255, 0.1)
  }
  // 进度条
  .progress-wrapper{
    display: flex
    width: 85%
    margin: 0px auto
    padding: 10px 0
    align-items: center
    line-height: 40px
    position: relative
  }
  .time{
    width: 30px
    color: white
  }
  .progress-bar-wrapper{
    flex: 1
  }
  
  .mini-player .head-icon{
    flex: 0 0 40px
    width: 40px
    height: 40px
    padding: 0 10px 0 20px
  }
  img{
    border-radius: 50%
  }
  .text{
    display: flex
    flex-direction: column
    justify-content: center
    flex: 1
    line-height: 20px
    overflow: hidden
  }
  .name{
    margin-bottom: 2px
    no-wrap()
    font-size: $font-size-medium
    color: black
  }
  .desc{
    no-wrap()
    font-size: $font-size-small
    color: black
  }
  .control{
    position: relative
    flex: 0 0 30px
    width: 30px
    padding: 0 10px
  }
  .control .icon{
    color: rgba(0,0,0,.6)
  }
  .icon-play-mini, .icon-pause-mini, .icon-playlist{
    font-size: 30px
    color: $color-theme-d
  }
  .icon-mini{
    font-size: 32px
    position: absolute
    left: 0
    top: 0
  }
  .progress-circle-wrapper{
    position: absolute
    top: 0
    left: 50%
    transform: translate(-50%,0)
    // width: 30px
    // height: 30px
  }
  
  // 图片转动
    .img-wrapper.play, .image.play{
      animation: rotate 20s infinite linear
      -webkit-animation: rotate 20s infinite linear
    }
    .img-wrapper.paused, .image.paused{
      animation-play-state: paused
    }
    @keyframes rotate{
      0%{
        transform: rotate(0)
      }
      100%{
        transform: rotate(360deg)
      }
    }
    
    .middle-r{
      display: inline-block
      height: 100%;
      position: absolute
      top: 0
      width: 100%
      overflow: hidden
    }
    .lyric-wrapper{
      width: 80%
      margin: 0 auto
      text-align: center
    }
    .lyric-text{
      line-height: 32px
      color: $color-text-l
      font-size: $font-size-medium
    }
    .current.lyric-text{
      color: white
    }
    
    .dot-wrapper{
      text-align: center
      font-size: 0
    }
    .dot{
      display: inline-block
      vertical-align: middle
      margin: 0 4px
      width: 8px
      height: 8px
      border-radius: 50%
      background: $color-text-l
    }
    &.active{
      width: 20px
      border-radius: 5px
      background: $color-text-ll
    }
</style>


