<template>
	<transition name="slider">
    <music-list :songs="singerSonges" :bgurl="singer.headIcon" :title="title" @loadMoreSong="loadMoreSong"></music-list>
  </transition>
</template>

<script type="text/javascript">
  import { getSingerDetail} from 'api/singer'
  import { getMusic} from 'api/song'
  import { ERR_OK } from 'api/config'
  import { mapGetters, mapMutations, mapActions } from 'vuex'
  import { creatSong, processSongsUrl} from 'common/js/song'
  import musicList from '../music-list/musicList'
	export default {
    computed: {
      ...mapGetters(['singer']),
      title() {
        return this.singer.name
      }
    },
    components: {
      musicList
    },
    data () {
      return {
        singerSonges: [],
        moreSonges: [],
        page: 0
      }
    },
    methods: {
      _normalLizeSong(musicData) {
        let songItem = creatSong(musicData)
        this.moreSonges.push(songItem)
      },
      loadMoreSong(){
        this.moreSonges = []
        this.page++
        this.getSong(this.page)
        setTimeout(()=>{
          this.singerSonges = this.singerSonges.concat(this.moreSonges)
        },500)
      },
      getSong(page){
        getSingerDetail(this.singer.id, page).then((res) => {
          if (res.code === ERR_OK) {
            res.data.list.forEach((item) => {
              this._normalLizeSong(item.musicData)
            })
            processSongsUrl(this.moreSonges)
          }
        })
      }
    },
    created () {
      this.getSong(this.page)
      this.singerSonges = this.moreSonges
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  .slider-enter-active,.slider-leave-active{
    transition: all .3s
  }
  .slider-enter,.slider-leave-to{
    transform: translate3d(100%, 0, 0)
  }
</style>


