import { random, playMode, findIndex} from 'common/js/config'
import { saveSearch, deleteSearch, addSong} from 'common/js/searchHistory'
const actions = {
  selectPlay({commit, state}, {list, index}) {
    commit('SET_SEQUENCE_LIST', list)
    if (state.playMode === 2) {
      let randomList = random(list)
      commit('SET_PLAY_LIST', randomList)
      index = findIndex(randomList, list[index])
    }else{
      commit('SET_PLAY_LIST', list)
    }
    commit('SET_FULL_SCREEN', true)
    commit('SET_PLAYING', true)
    commit('SET_CUREENT_INDEX', index)
  },

  randomPlay({commit, state}, {list}) {
    commit('SET_PLAY_MODE', playMode.random)
    let randomList = random(list)
    commit('SET_SEQUENCE_LIST', list)
    commit('SET_CUREENT_INDEX', 0)
    commit('SET_PLAY_LIST', randomList)
    commit('SET_FULL_SCREEN', true)
    commit('SET_PLAYING', true)
  },
  
  resetPlayList({commit, state}, {list}){
    if (state.playMode === playMode.random) {
      let randomList = random(list)
      commit('SET_PLAY_LIST', randomList)
      let index = findIndex(randomList, list[state.currendIndex])
      commit('SET_CUREENT_INDEX', index)
    }
    if (state.playMode === playMode.sequence) {
      let sequenceList = state.sequenceList
      commit('SET_PLAY_LIST', sequenceList)
      let index = findIndex(sequenceList, list[state.currendIndex])
      commit('SET_CUREENT_INDEX', index)
    }
  },

  saveSearchHistory({commit, state}, query) {
    commit('SET_SEARCH_HISTORY', saveSearch(state.searchHistory, query))
  },

  deleteSearchHistory({commit, state}, query) {
    commit('SET_SEARCH_HISTORY', deleteSearch(state.searchHistory, query))
  },

  clearSearchHistory({commit, state}) {
    commit('SET_SEARCH_HISTORY', [])
  },

  insertSong({commit, state}, song) {
    let currentIndex = state.currendIndex
    let sequenceList = state.sequenceList
    let playList = state.playList
    // 判断是否已经有相同的曲，有则删除
    let pIndex = findIndex(playList,song)
    currentIndex++
    // 插入这首歌到当前索引位置
    playList.splice(currentIndex, 0, song)
    if (pIndex > -1) {
      if (currentIndex > pIndex) {
        playList.splice(pIndex, 1)
        currentIndex--
      } else {
        playList.splice(pIndex+1, 1)
      }
    }
    let sIndex = findIndex(sequenceList,song)
    sequenceList.splice(currentIndex, 0, song)
    if (sIndex > -1) {
      let sIndex = findIndex(sequenceList, song)
      if (currentIndex > sIndex) {
        sequenceList.splice(sIndex, 1)
        currentIndex--
        console.log(1)
      } else {
        sequenceList.splice(sIndex+1, 1)
      }
    }
    commit('SET_CUREENT_INDEX', currentIndex)
    commit('SET_PLAY_LIST', playList)
    commit('SET_SEQUENCE_LIST', sequenceList)
    commit('SET_FULL_SCREEN', true)
    commit('SET_PLAYING', true)
  },
  // 清空播放列表
  clearPlay({commit, state}) {
    commit('SET_PLAYING', false)
    commit('SET_PLAY_LIST', [])
    commit('SET_SEQUENCE_LIST', [])
    commit('SET_FULL_SCREEN', false)
  },
  // 删除播放列表中的某个歌曲
  deleteSongItem({commit, state}, song) {
    let currentIndex = state.currendIndex
    let sequenceList = state.sequenceList
    let playList = state.playList

    let pIndex = findIndex(playList, song)
    playList.splice(pIndex, 1)
    console.log(sequenceList.length, playList.length)
    // let sIndex = findIndex(sequenceList, song)
    // sequenceList.splice(sIndex, 1)
    if (currentIndex > pIndex || currentIndex === playList.length) {
      currentIndex--
    }
    commit('SET_PLAY_LIST', playList)
    commit('SET_SEQUENCE_LIST', sequenceList)
    commit('SET_CUREENT_INDEX', currentIndex)
    if (!playList.length) {
      commit(SET_PLAYING_STATE, false)
    } else {
      commit(SET_PLAYING_STATE, true)
    }
  },
  playListSong({commit, state}, song){
    let playList = state.playList
    let pIndex = findIndex(playList,song)
    commit('SET_CUREENT_INDEX', pIndex)
    commit('SET_FULL_SCREEN', true)
    commit('SET_PLAYING', true)
  },
  selectFavoriteSong({commit, state}, song){
    let favoriteList = state.favoriteList
    let index = findIndex(favoriteList, song)
    if(index >= 0){
      favoriteList.splice(index, 1)
    }else{
      favoriteList.unshift(song)
    }
    commit('SET_FAVORITE_LIST', favoriteList)
  },
  selectHeardList({commit, state}, song){
    let heardList = state.heardList
    let pIndex = findIndex(heardList, song)
    if (pIndex > -1) {
      heardList.splice(pIndex, 1)
    }
    heardList.unshift(song)
    commit('SET_HEARD_LIST', heardList)
  }
}

export default actions





