<template>
	<div class="progress" @click="progressClick">
		<div class="progress-bar"
		     @touchmove="touchmove"
		     ref="progressBar"
		     @touchstart.stop="touchstart"
		     @touchend="touchend">
			<div class="progress-inner" ref="progressInner"></div>
			<div class="progress-btn" ref="progressBtn"></div>
		</div>
	</div>
</template>

<script type="text/javascript">
	export default {
		props: {
			percent: {
				type: Number,
			  default: 0
			}
		},
		data() {
			return {
				isMove: false
			}
		},
		methods: {
			touchstart(e){
				this.touch.initiated = true
				let moveStartX = e.touches[0].pageX
        let touchX = moveStartX - this.$refs.progressBar.offsetLeft
        // this.currentPercent = touchX / this.$refs.progressBar.offsetWidth
        // this.$emit('changeCurrentTime', this.currentPercent)
			},
			touchmove(e) {
				if (!this.touch.initiated) {
					return
				}
				let moveStartX = e.changedTouches[0].pageX
				let touchX = moveStartX - this.$refs.progressBar.offsetLeft
        this.currentPercent = touchX / this.$refs.progressBar.offsetWidth
				if (this.currentPercent > 1) {
					this.currentPercent = 1
				}
				if (this.currentPercent < 0) {
					this.currentPercent = 0
				}
        this.setPos(this.currentPercent)
        this.$emit('percentChange', this.currentPercent)
				this.isMove = true
			},
			touchend(){
				this.touch.initiated = false
        this.$emit('changeCurrentTime', this.currentPercent)
			},
			setPos(newPercent) {
				if (this.isMove) {
					this.isMove = false
					return 
				}
				let progressBarWidth = this.$refs.progressBar.offsetWidth
        const progressInner = this.$refs.progressInner
        const progressBtn = this.$refs.progressBtn
        progressInner.style.width = progressBarWidth * newPercent +'px'
        progressBtn.style.left = progressBarWidth * newPercent - 8 +'px'
			},
			progressClick(e) {
        let moveStartX = e.pageX
        let touchX = moveStartX - this.$refs.progressBar.offsetLeft
        this.currentPercent = touchX / this.$refs.progressBar.offsetWidth
        this.$emit('changeCurrentTime', this.currentPercent)
			}
		},
		watch: {
			percent(newPercent){
        this.setPos(newPercent)
			}
		},
    created() {
      this.touch = {}
    },
	}
</script>

<style type="text/css">
	.progress{
		height: 5px;
		width: 90%;
		margin: 0 auto;
	}
	.progress-bar{
		position: relative;
		
		background: rgba(0, 0, 0, 0.3);
		height: 4px;
	}
	.progress-inner{
		position: absolute;
		height: 100%;
		width: 0px;
		background: #ffcd32
	}
	.progress-btn{
		position: absolute;
		top: -6px;
		box-sizing: border-box;
		width: 16px;
    height: 16px;
    left: -8px;
		background: #ffcd32;
		border: 3px solid white;
		border-radius: 8px;
	}
</style>


