<template>
  <div class="search-box">
    <i class="icon-search"></i>
    <input ref="query" class="box" v-model="query" placeholder="搜索歌曲、歌手">
    <i @click="clear" class="icon-dismiss"></i>
  </div>
</template>

<script type="text/javascript">
  export default {
    data() {
      return {
        query: ''
      }
    },
    props: {
      updataquery: ''
    },
    methods: {
      clear() {
        this.query = ''
      },
      debounce(func, delay) {
        let timer
        return function (...args) {
          if (timer) {
            clearTimeout(timer)
          }
          timer = setTimeout(() => {
            func.appy(this, argus)
          },delay)
        }
      }
    },
    watch: {
      query(newVal) {
        this.debounce(this.$emit('query', newVal),200)
      },
      updataquery(newval) {
        this.query = newval
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .search-box
    display: flex
    align-items: center
    box-sizing: border-box
    padding: 0 6px
    height: 35px
    background: #ebecec  
    border-radius: 10px
    .icon-search
      font-size: 24px
      color: rgba(0,0,0,.4)
    .box
      flex: 1
      margin: 0 5px
      line-height: 18px
      background: #ebecec 
      font-size: $font-size-medium
      outline: 0
      &::placeholder
        color: rgba(0,0,0,.5)
    .icon-dismiss
      font-size: 16px
      color: rgba(0,0,0,.5)

</style>


