<template>
	<div class="slider" ref="slider">
		<div class="slider-group" ref="sliderGroup">
			<slot></slot>
		</div>
		<div class="dots">
			<span v-for="(dot,index) in dots" class="dot" :class="{dotActive: currentIndex === index}"></span>
		</div>
	</div>
</template>

<script type="text/javascript">
	import BScroll from 'better-scroll'
	import {addClass} from 'common/js/addClassName'

	export default {
		props: {
			loop: {
				type: Boolean,
				default: true
			},
			autoPlay: {
				type: Boolean,
				default: true
			},
			interval: {
				type: Number,
				default: 3000
			}
		},
		data() {
			return {
				dots: [],
				currentIndex: 0
			}
		},
		methods: {
			_setSliderWidth(resize) {
				this.children = this.$refs.sliderGroup.children
				let width = 0
				let sliderWidth = this.$refs.slider.offsetWidth
				// console.log(sliderWidth)
				for (var i = this.children.length - 1; i >= 0; i--) {
					let child = this.children[i]
                    addClass(child,"slider-item")
                    child.style.width = sliderWidth + 'px'
                    width += sliderWidth
				}
                // 要无缝轮播
				if (this.loop && !resize) {
				  width += 2*sliderWidth
				}
				this.$refs.sliderGroup.style.width = width + 'px'
			},
			_initSlider() {
				this.slider = new BScroll(this.$refs.slider,{
					scrollX: true,
					scrollY: false,
					// 惯性
					momentum: false,
					snap: {
						// 循环
						loop: this.loop,
					    threshol: 0.3,
					    speed: 400
					},
					click: true
				})
				this.slider.on('scrollEnd',()=> {
				    // console.log(this.slider)
					let pageIndex = this.slider.currentPage.pageX
					if (this.loop) {
						pageIndex -= 1
					}
					this.currentIndex = pageIndex

					if (this.autoPlay) {
						clearTimeout(this.timer)
						this._autoPlay()
					}
				})
			},
			_initDots() {
				this.dots = new Array(this.children.length)
			},
			_autoPlay() {
				let pageIndex = this.currentIndex + 1
				this.timer = setTimeout(() => {
					this.slider.next(400)
					// this.slider.goToPage(pageIndex, 0, 400)
				}, this.interval)
			}
		},
		mounted() {
			this.$nextTick(() => {
				this._setSliderWidth()
				this._initDots()
				this._initSlider()
				if (this.autoPlay) {
					this._autoPlay()
				}
			})
			window.addEventListener('resize',() => {
                if (!this.slider) {
                	return
                }
                this._setSliderWidth(true)
                this.slider.refresh()
			})
		},
		destroyed() {
			clearTimeout(this.timer)
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  
  .slider
  	min-height: 1px
  	// position: relative
  	.slider-group
  		position: relative
  		overflow: hidden
  		white-space: nowrap
  		.slider-item
  			float: left
  			box-sizing: border-box
  			text-align: center
  			overflow: hidden
  			a
  				display: block
  				width: 100%
  				overflow: hidden
  				text-decoration: none
  			img
  				width:100%
  				display: block
  	.dots
  		position: absolute
  		right: 0
  		left: 0
  		bottom: 12px
  		transform: translateZ(1px)
  		text-align: center
  		font-size: 0
  		.dot
  			display: inline-block
  			margin: 0 4px
  			width: 8px
  			height: 8px
  			background: $color-text-l
  			border-radius: 50%
  		.dotActive
  			width: 20px
  			border-radius: 5px
  			background: $color-text-ll
</style>


