<template>
	<div class="recommend">
		<scroll class="recommend-content" :data="discList" ref="recommendContent" :pullup="true" @scrollToEnd="moreDiscList" :probeType="3">
			<div>
				<div v-if="recommends.length"  class="slider-wrapper">
					<div class="slider-content">
						<slider ref="slider">
						    <div v-for="item in recommends">
							   	<a :href="item.linkUrl">
							   	 	<img :src="item.picUrl">
							   	</a>
						   	</div>
					    </slider>
					</div>
				</div>
				<div class="recommend-list">
					<h1 class="list-title">热门歌单</h1>
					<ul class="">
						<li class="item" v-for="item in discList" @click="handleDisc(item)">
							<div class="icon">
								<img v-lazy="item.imgurl" alt="">
							</div>
							<div class="text">
								<h2 class="name">{{item.creator.name}}</h2>
								<p class="desc">{{item.dissname}}</p>
							</div>
						</li>
						<loading v-show="searchMore"></loading>
					</ul>
				</div>
		    </div>
		    <div class="loading-contanier" v-show="!discList.length">
		    	<loading></loading>
		    </div>
		</scroll>
		<router-view></router-view>
	</div>
</template>

<script type="text/javascript">
	import Axios from 'axios'
	import {getRecommend, getPlayList, getDiscList} from 'api/recommend'
	import {ERR_OK} from 'api/config'
	import Slider from 'base/slider/slider'
	import Scroll from 'base/scroll/scroll'
	import Loading from 'base/loading/loading.vue'
  import { playlistMixin } from 'common/js/mixin'
  import { mapMutations } from 'vuex'
	export default {
    mixins: [playlistMixin],
		name: 'recommend',
		components: {
			Slider,
			Scroll,
			Loading
		},
		data() {
			return {
				recommends:[],
				discList:[],
				cdList: [],
				discListPage: 0,
				searchMore: false
			}
		},
		created () {
	      this._getRecommend()
	      this._getPlayList(this.discListPage)
			},
		computed: {
		},
		methods: {
			...mapMutations(['SET_CURRENT_DISC']),
	    _getRecommend() {
        getRecommend().then((res) => {
          if (res.code === ERR_OK) {
            this.recommends = res.data.slider
          }
        })
	    },
	    _getPlayList(pageNum) {
	    	let sin = pageNum * 29
	    	let ein = (pageNum + 1) * 29
        getPlayList(sin, ein).then((res) => {
        	if (res.data.code === 0) {
          	let list = res.data.data.list
            this.discList = this.discList.concat(list)
          }
        })
	    },
	    handlePlayList(playList) {
          let bottom = playList.length ? '50px' : '0px'
	    	console.log(bottom)
          this.$refs.recommendContent.$el.style.bottom = bottom
          this.$refs.recommendContent.refresh()
      },
      handleDisc(item){
      	this.$router.push({
      		path: `/recommend/${item.dissid}`
      	})
      	this.SET_CURRENT_DISC(item)
      },
      moreDiscList() {
        this.searchMore = true
        this._getPlayList(this.discListPage + 1)
      }
		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  .recommend
  	.recommend-content
	  	position: fixed
	  	width: 100%
	  	top: 88px
	  	bottom: 0
  		overflow: hidden
  		.slider-wrapper
	        position: relative
	        width: 100%
	        height: 0
	        padding-top: 40%
	        overflow: hidden
	        .slider-content
	          position: absolute
	          top: 0
	          left: 0
	          width: 100%
	          height: 100%
	    .recommend-list
        .list-title
          height: 50px
          line-height: 50px
          text-align: center
          font-size: $font-size-l
          font-weight: bold
          color: black
	        .item
	        	display: flex
	        	padding: 5px 20px
	        	align-items: center
	        	box-sizing: border-box
	        	border-top: 1px solid rgba(233,233,233,.5)
	        	.icon
	        		flex: 0 0 60px
	        		width: 60px
	        		padding-right: 10px
	        		img
	        			width: 100%
	        			border-radius: 5px
	        	.text
	        		display: flex
	        		justify-content: center
	        		flex-direction: column
	        		line-height: 20px
	        		flex: 1
	        		overflow: hidden
	        		font-size: $font-size-medium
	        		.name
	        			margin-bottom: 10px
	        			color: black
	    			.desc
	    				color: black
	    				
	.loading-contanier{
		position: relative
		top: 100px
	}
</style>


