const state = {
	singer (state) {
		return state.singer
	},
	playList (state) {
		return state.playList
	},
	fullScreen (state) {
		return state.fullScreen
	},
	playMode (state) {
		return state.playMode
	},
	playing (state) {
		return state.playing
	},
	sequenceList (state) {
		return state.sequenceList
	},
	currendIndex (state) {
		return state.currendIndex
	},
	currendSong (state) {
		return state.playList[state.currendIndex] || {}
	},
	currentDisc (state) {
		return state.currentDisc
	},
	currentTopList (state) {
		return state.currentTopList
	},
	searchHistory (state) {
		return state.searchHistory
	},
	searchQuery (state) {
		return state.searchQuery
	},
	favoriteList (state) {
		return state.favoriteList
	},
	heardList (state) {
		return state.heardList
	}
}

export default state


