<template>
	<div class="tab">
		<router-link to="/recommend" tag="div" class="tab-item">
			<span class="tab-link">推荐</span>
		</router-link>
		<router-link to="/singer" tag="div" class="tab-item">
			<span class="tab-link">歌手</span>
		</router-link>
		<router-link to="/rank" tag="div" class="tab-item">
			<span class="tab-link">排行</span>
		</router-link>
		<router-link to="/search" tag="div" class="tab-item">
			<span class="tab-link">搜索</span>
		</router-link>
	</div> 
</template>

<script type="text/javascript">
	export default {
		name: 'tab'
	}
</script>

<style  type="text/css" scoped>
    /* @import "../../common/stylus/variable"*/
	.tab{
		display: flex;
		height: 44px;
		line-height: 44px;
		font-size: 14px;
        background: rgba(235,0,0,.7)
    }
	.tab-item{
		flex: 1;
		text-align: center;
	}
	.tab-link{
		padding: 5px;
		color: white;
		font-size: 15px
	}
    .router-link-active .tab-link{
        color: white;
        border-bottom: 2px solid white;
        font-weight: bold;
    }
</style>


