<template>
  <div class="singer-detail">
    <!-- <div class="singer-head" ref="singerHead"> -->
      <h3 class="singer-name"  ref="singerName">
        <span class="back-btn" @click="handleBack"><i class="icon-back"></i></span>
        <span class="name">{{title}}</span>
      </h3>
      <div class="singer-head-mask" :style="{backgroundImage: `url(${bgurl})`}" ref="singerHeadBg">
      <p class="play-music" v-show="showPlay" @click="randomPlayList">
        <span class="play-btn"><i class="icon-play"></i></span>
        <span class="text">随机播放全部</span>
      </p>
      </div>
      <!-- <img :src='singer.headIcon'> -->
      
    <!-- </div> -->
    <div class="bg-layer" ref="layer"></div>
    <scroll class="music-list" :data="songs" :listenScroll="true" :probeType="3" @scroll="scroll" ref="musicList" :pullup="true" @scrollToEnd="scrollToEnd">
      <div class="song-list-wrapper">
        <song-list :songDataes="songs" @select="selectItem" :rank="rank" :isLoadMore="isLoadMore"></song-list>
      </div>
    </scroll>
  </div>
</template>

<script type="text/javascript">
  import { mapGetters, mapMutations, mapActions } from 'vuex'
  import scroll from 'base/scroll/scroll'
  import SongList from 'base/song-list/song-list'
  import { playlistMixin } from 'common/js/mixin'
	export default {
    mixins: [playlistMixin],
    props: {
      songs: {
        required: true
      },
      bgurl: {
        required: true
      },
      title: {
        required: true
      },
      rank: {
        default: false
      }
    },
    components: {
      SongList,
      scroll
    },
    computed: {
      ...mapGetters(['singer']),
      
    },
    data () {
      return {
        scrollY: 0,
        maxTranslateY: 0,
        showPlay: true,
        isLoadMore: false
      }
    },
    methods: {
      ...mapMutations({
        setPlaying: 'SET_PLAYING',
        setPlayList: 'SET_PLAY_LIST'
      }),
      ...mapActions(['selectPlay', 'randomPlay']),
      playMusic(song) {
        // this.songlist.push(song)
        this.setPlayList(song)
        this.setPlaying(true)
      },
      selectItem(item, index){
        this.selectPlay({
          list: this.songs,
          index
        })
      },
      randomPlayList() {
        this.randomPlay({
          list: this.songs
        })
      },
      handleBack() {
        this.$router.go(-1)
      },
      scroll(pos) {
        
        this.scrollY = -pos.y
      },
      layerTranslate(newval) {

        let translateY = Math.min(this.maxTranslateY, newval)
        this.$refs.layer.style.transform = `translate3d(0, ${-translateY}px, 0)`
        if (newval > this.maxTranslateY) {
          this.showPlay = false
          this.$refs.singerHeadBg.style.height = this.$refs.singerName.offsetHeight + 'px'
          this.$refs.singerHeadBg.style['z-index'] = 1
        } else {
          this.showPlay = true
          this.$refs.singerHeadBg.style.height = this.singerHeadBgHeight + 'px'
          this.$refs.singerHeadBg.style['z-index'] = 0
        }
      },
      scaleHeadBg(newval) {
        if (newval < 0) {
          let scalePercent = -newval/this.singerHeadBgHeight + 1
          this.$refs.singerHeadBg.style['z-index'] = 1
          this.$refs.singerHeadBg.style.transform = `scale(${scalePercent})`
        }
      },
      handlePlayList(playList) {
        let bottom = playList.length ? '50px' : 0
        this.$refs.musicList.$el.style.bottom = bottom
        this.$refs.musicList.refresh()
      },
      scrollToEnd(){
        this.$emit("loadMoreSong")
        this.isLoadMore = true
      }
    },
    watch: {
      scrollY(newval) {
        if(newval>0){
          // 下拉歌曲列表时，歌手图片放大
          this.layerTranslate(newval)
        }
        if (newval < 0) {
          this.scaleHeadBg(newval)
          // 向上滑动时，列表上移动
        }
      }
    },
    mounted() {
      this.maxTranslateY = this.$refs.singerHeadBg.offsetHeight - this.$refs.singerName.offsetHeight
      this.singerHeadBgHeight = this.$refs.singerHeadBg.offsetHeight
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  @import "../../common/stylus/mixin"
  .singer-detail{
  	position: fixed
  	top: 0
  	bottom: 0
  	width: 100%
    left: 0 
    background: white 
    }
  .singer-head{
    width: 100%
    text-align: center
    position: relative
  }
  .singer-head-mask{
    background-size: cover
    background-repeat: no-repeat
    position: absolute
    top: 0
    height: 250px
    width: 100%
  }
  .singer-name{
    color: $color-text
    font-size: $font-size-large-x
    line-height: 40px
    height: 40px
    position: relative
    width: 100%
    z-index:2
    text-align: center
  }
  .play-music{
    width: 170px
    height: 35px
    line-height: 35px
    border-radius: 35px
    border: 2px solid $color-theme
    color: $color-theme
    font-size: $font-size-medium
    font-size: 18px
    top: 180px
    left: 50%
    transform: translate(-50%,0)
    position: absolute
    display: flex
  }
  .play-btn{
    font-size: 20px
    width: 45px
    text-align: center
  }
  .back-btn{
    color: white
    float: left
    font-size: $font-size-large-x
    padding-left: 10px
    position: absolute
    left: 0
  }
  .text{
    flex: 1
  }
  .bg-layer{
    position: absolute
    top: 250px
    bottom:0
    width: 100%
    background: white
  }
  .music-list{
    position: absolute
    top: 250px
    bottom: 0
    width: 100%
  }
  .name{
    display: inline-block
    width: 70%
    font-size: 20px
    no-wrap() 
  }
</style>


