<template>
  <scroll class="search-result-wrapper" :data="songList" :pullup='true' @scrollToEnd="searchMore" ref="searchResultWrapper" :probeType="3">
    <div>
      <ul class="singer-list">
        <li class="item" v-show="isZhidao" @click="select">
          <i class="icon-mine icon"></i>
          <span class="text">{{zhida.singername}}</span>
        </li>
      </ul>
      <ul class="song-list" v-if="songList.length">
        <li class="item" v-for="item in songList" @click="playMusic(item)">
          <i class="icon-music icon"></i>
          <span class="text" v-html="item.name">
          </span>
          <span class="name">
             &nbsp;- {{item.singer}}
          </span>
        </li>
        <loading v-show="hasMore" title=""></loading>
      </ul>
      <div class="loading-container">
        <loading v-if="!songList.length"></loading>
      </div>
    </div>
  </scroll>
</template>

<script type="text/javascript">
  import { getSearchList } from 'api/search'
  import { ERR_OK } from 'api/config'
  import { mapActions } from 'vuex'
  import { creatSong, processSongsUrl} from 'common/js/song'
  import Loading from 'base/loading/loading'
  import Scroll from 'base/scroll/scroll'
  import { playlistMixin } from 'common/js/mixin'
  export default {
    mixins: [playlistMixin],
    props: {
      query: {
        default: ''
      }
    },
    components: {
      Loading,
      Scroll
    },
    data() {
      return {
        isZhidao: false,
        zhida: {},
        songList: [],
        page: 1,
        moreSearch: [],
        hasMore: false
      }
    },
    methods: {
      ...mapActions([
        'insertSong'
      ]),
      _normalLizeSong(musicData) {
        let songItem = creatSong(musicData)
        this.moreSearch.push(songItem)
      },
      searchMore() {
        this.hasMore = true
        this.moreSearch = []
        this.page ++
        getSearchList(this.query, this.page).then(res=>{
          if (res.data.code === ERR_OK) {
            let searchResult = res.data.data
            if (searchResult.zhida.singermid) {
              this.isZhidao = true
              this.zhida = searchResult.zhida
            }else{
              this.isZhidao = false
            }
            let list = searchResult.song.list
            
            list.forEach((item) => {
              this._normalLizeSong(item)
            })
            
            processSongsUrl(this.moreSearch)
            }
            this.songList = this.songList.concat(this.moreSearch)
            this.hasMore = false
        })
        
      },
      search(query) {
        getSearchList(query, this.page).then(res=>{
          if (res.data.code === ERR_OK) {
            let searchResult = res.data.data
            if (searchResult.zhida.singermid) {
              this.isZhidao = true
              this.zhida = searchResult.zhida
            }else{
              this.isZhidao = false
            }
            let list = searchResult.song.list
            this.moreSearch = []
            list.forEach((item) => {
              this._normalLizeSong(item)
            })
            this.songList = this.moreSearch
            processSongsUrl(this.songList)
            }
        })
      },
      select() {
        // 设置歌手的信息
        let singer = {
          id: this.zhida.singermid,
          name: this.zhida.singername,
          headIcon: 'http://y.gtimg.cn/music/photo_new/T001R300x300M000' + this.zhida.singermid + '.jpg?max_age=2592000'
        }
        this.$emit("selectSinger",singer)
      },
      creatSongItem(item) {
        let songs = []
        songs.push(creatSong(item))
        processSongsUrl(songs)
        return songs[0]
      },
      playMusic(item){
        this.insertSong(item)
      }
    },
    watch: {
      query(newVal) {
        this.search(newVal)
      }
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .search-result-wrapper{
    height: 100%
    // position: fixed
    // top: 0
    // left: 0
    // bottom: 0
    // width: 100vw
    // padding: 0 30px
    // overflow: hidden
    // z-index: 999
    // box-sizing: border-box
  }
  .singer-list{
    box-sizing: border-box
  }
  .item{
    display: flex    
    color: black
    padding: 5px 0
    line-height: 20px
    border-bottom: 1px solid rgba(233,233,233,0.5)
  }
  .icon{
    width: 30px
    text-align: left
    font-size: 16px
    line-height: 20px
  }
  .text{    
    color: black
    font-size: 15px
    no-wrap()
  }
  .name{
    font-size: 12px
    color: gray
    flex: 1
    no-wrap()
    vertical-align: bottom
  }
  .loading-container{
    margin-top: 160px
  }
</style>



