import { playMode} from 'common/js/config'
const state = {
  singer: [],
  playList: [],//播放列表
  sequenceList: [],//顺序播放列表
  fullScreen: false,//全屏
  playMode: playMode.sequence,//播放模式
  playing: false,//播放或暂停
  currendIndex: -1,//当前正在播放的歌曲的索引
  currentDisc: [],
  currentTopList: [],
  searchHistory: [],
  searchQuery: '',
  favoriteList: [],
  heardList: []
}

export default state


